class Init{

	constructor(  )
	{
		this.eventListener;
		this.eventTarget;
		this.eventContext;
		this.callable;
	}

	setCallable( callable )
	{
		let callableIsFunction = !!(callable && callable.constructor && callable.call && callable.apply);
		if( callableIsFunction )
			this.callable = callable;

		this.checkInstantiation();

		return this;
	}

	checkInstantiation(  )
	{
		if( this.eventListener && this.eventTarget && this.callable)
			this.eventTarget.addEventListener(this.eventListener, (event) => {
				let context = this || this.eventContext;
				let func = this.callable.bind(context, event);

				func();
			});
	}

	setEventListener( listener )
	{
		this.eventListener = listener;

		this.checkInstantiation();

		return this;
	}

	setEventTarget( target )
	{
		this.eventTarget = target;

		this.checkInstantiation();

		return this;
	}

	setEventContext( thisVar )
	{
		this.eventContext = thisVar;
		this.checkInstantiation();

		return this;
	}
}